import React, { useState, useEffect } from "react";
import { Button, Space, Table,PageHeader } from "antd";
import { harcamaService } from "../services/harcama.services";
import { PlusSquareOutlined } from "@ant-design/icons";

const CyclomediaList = (props) => {
  const [state, setState] = useState({ dataList: [] });

  useEffect(() => {
    harcamaService.CyclomediaList().then((data) => {
      setState({
        dataList: data,
        selectedRowKeys: [],
      });
    });
  }, []);

  const columns = [
    {
      title: "ID",
      dataIndex: "ObjectID",
      key: "ObjectID",
    },
    {
      title: "KURUM",
      dataIndex: "kurum",
      key: "kurum",
    },
    {
      title: "APIKEY",
      dataIndex: "apikey",
      key: "apikey",
    },

    {
      title: "USERS",
      dataIndex: "users",
      key: "users",
    },
    {
      title: "Password",
      dataIndex: "password",
      key: "password",
    },
    {
      title: "Tarih",
      dataIndex: "m_DATE",
      key: "m_DATE",
    },
  ];

  const onSelectChange = (selectedRowKeys) => {
    console.log("selectedRowKeys changed: ", selectedRowKeys);
    setState({ ...state, selectedRowKeys });
  };

  const rowSelection = {
    selectedRowKeys: state.selectedRowKeys,
    onChange: onSelectChange,
  };

  const summary = (pageData) => {
    let sum = 0;
    if (pageData.length > 0) {
      sum = pageData
        .map((item) => item.miktar)
        .reduce((prev, next) => prev + next);
    }

    return (
      <Table.Summary fixed>
        <Table.Summary.Row>
          <Table.Summary.Cell index={0}></Table.Summary.Cell>
          <Table.Summary.Cell index={1}></Table.Summary.Cell>
          <Table.Summary.Cell index={2}></Table.Summary.Cell>
          <Table.Summary.Cell index={3}>Miktar :{sum}</Table.Summary.Cell>
          <Table.Summary.Cell index={5}></Table.Summary.Cell>
        </Table.Summary.Row>
      </Table.Summary>
    );
  };

  return (
    <>
      <Space>
        <Button type="primary" icon={<PlusSquareOutlined />}>Yeni Kayıt</Button>
        <PageHeader title="Cyclomedia Liste Ekranı"/>
      </Space>
      <Table
        rowSelection={rowSelection}
        dataSource={state.dataList}
        columns={columns}
        summary={summary}
      />
    </>
  );
};

export default CyclomediaList;
